.ant-tabs-content-holder {
  min-height: 600px;
}

.ant-tabs-tab .ant-tabs-tab-btn > span {
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}

.ant-tabs-tab .ant-tabs-tab-btn .anticon {
  font-size: clamp(1.5rem, 4vw, 3rem);
}

.ant-tabs-nav-list {
  white-space: normal;
}

.ant-tabs-tabpane pre {
  padding: 1rem;
  border: 1px solid #ddd;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.4);
  margin: 0.5rem;
}
