.PrivateTripsContainer {
  background: rgba(0, 0, 0, 0.2) url("/images/mountain.jpg") no-repeat fixed;
  background-size: cover;
}

.PrivateTripsContainer h1,
.PrivateTripsContainer h3 {
  color: #fff;
}

.PrivateTripsContainer .ant-form-item-label label {
  color: #ddd !important;
}

.PrivateTrips {
  min-height: 100vh;
  max-width: 1200px;
  margin: auto;
  padding: 5rem 1rem;
}
.PrivateTrips .detailsCol {
  text-shadow: 2px 2px 2px black;
}
.PrivateTrips .formCol {
  background: rgba(0, 0, 0, 0.5);
  padding: 1rem;
}

.ant-select-selection-item img,
.ant-select-item-option-content img {
  width: 20px;
}
.ant-picker {
  width: 100%;
}
